import React, { useEffect, useRef } from "react";
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";

const Model = () => {
  let canvasWrapper = useRef(null);

  const initScrollHandler = pivot => {
    window.addEventListener("scroll", () => {
      const scrolled = window.scrollY;
      pivot.rotation.x = 6.8 + scrolled * -0.0022;
      pivot.rotation.y = 0 + scrolled * -0.00055;
      pivot.rotation.z = -0.5 + scrolled * 0.00055;
      pivot.position.y = 0 + scrolled * -0.05;
    });
  };

  const playAnimation = pivot => {
    const tCanvas = new TimelineMax(); // eslint-disable-line
    tCanvas
      .fromTo(
        canvasWrapper,
        2,
        { y: 1500 },
        { y: 0, ease: 'Power4.easeOut' },
        "+=.5"
      )
      .fromTo(
        pivot.rotation,
        2.2,
        { x: 3, y: -1, z: -1, autoAlpha: 0 },
        { x: 6.8, y: 0, z: -0.5, autoAlpha: 1, ease: 'Power3.easeOut' },
        "-=2"
      )
      .call(initScrollHandler(pivot), [], this, 2);
  };

  // Component did mount
  useEffect(() => {
    const width = 900,
      height = 900,
      canvas = document.getElementById("canvas");
    let mesh,
      pivot = new THREE.Object3D();
    //RENDERER
    const renderer = new THREE.WebGLRenderer({
      canvas: canvas,
      antialias: true,
      alpha: true
    });
    renderer.setClearColor(0x000000, 0);
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.setSize(width, height);
    renderer.shadowMap.enabled = true;
    renderer.shadowMap.type = THREE.BasicShadowMap;
    const camera = new THREE.PerspectiveCamera(
      35,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    camera.aspect = width / height;
    camera.updateProjectionMatrix();
    const scene = new THREE.Scene();
    const ambientLight = new THREE.AmbientLight(0xffffff, 0.9);
    scene.add(ambientLight);

    const loader = new GLTFLoader();
    loader.load("/model/scene.gltf", gltf => {
      mesh = gltf.scene;
      var box = new THREE.Box3().setFromObject(mesh);
      box.getCenter(mesh.position); // this re-sets the mesh position
      mesh.position.multiplyScalar(-1);
      pivot.position.set(0, 0, -210);
      pivot.rotation.set(6.8, 0, -0.5);
      pivot.add(mesh);
      scene.add(pivot);
      playAnimation(pivot);
    });

    const render = async () => {
      await renderer.render(scene, camera);
      requestAnimationFrame(render);
    };

    render();
  }, []);

  return (
    <div ref={el => canvasWrapper = el}>
      <canvas id="canvas"></canvas>
    </div>
  );
};

export default Model;
