import React, { useRef, useEffect, useState } from "react";
import { BrowserRouter as Router, Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "../styles/tailwind.css";

const Navbar = props => {
  const [mobileNavOpen, setMobileNavOpen] = useState(false);

  const navItems = [
    { title: "Work", href: "/work" },
    { title: "About", href: "/about" },
    { title: "Contact", href: "/contact" }
  ];
  let animateNavItems = useRef([]);

  
  useEffect(() => {
    mobileNavOpen ? (
      document.body.style.overflow = 'hidden'
    ) : (
      document.body.style.overflow = 'unset'
    )
  }, [mobileNavOpen])
  

  useEffect(() => {
    const tNav = new TimelineMax(); // eslint-disable-line
    tNav.staggerFromTo(
      animateNavItems.current,
      0.3,
      { y: 25, autoAlpha: 0 },
      { y: 0, autoAlpha: 1 },
      0.2,
      "+=.5"
    );
  }, []);

  return (
    <nav className={`navbar navbar-expand-md ${!mobileNavOpen ? 'overflow-hidden absolute' : 'fixed'}`}>
      <Router forceRefresh={true}>
        <div className="container">
          <Link to="/" className="navbar-brand">
            <img
              className="px-8 lg:px-1"
              src="/images/flipbox-logo.png"
              style={{ height: "30px", paddingRight: "30px" }}
            />
          </Link>

          <div onClick={()=>setMobileNavOpen(!mobileNavOpen)} className="hamburger-menu block md:hidden">
            <div className={`bar bar-black ${mobileNavOpen ? 'animate' : ''}`}></div>
          </div>

          <div className={`mobile-nav ${mobileNavOpen ? 'show' : 'hide'}`}>
            <ul className="container items-center pt-40">
              {navItems.map((item, index) => (
                <li key={index} className="nav-item w-full py-16">
                  <Link
                    to={item.href}
                    className="heading-secondary nav-link nav-link-hover"
                  >
                    {item.title}
                  </Link>
                </li>
              ))}
            </ul>
          </div>

          <div className="collapse navbar-collapse" id="navbarNavDropdown">
            <ul className="navbar-nav align-items-center ml-auto">
              {navItems.map((item, index) => (
                <li
                  key={index}
                  ref={el => (animateNavItems.current[index] = el)}
                  className="nav-item"
                >
                  <a
                    href={item.href}
                    className={`nav-link nav-link-hover text-${props.color}`}
                  >
                    {item.title}
                  </a>
                </li>
              ))}
            </ul>

            {/* 
            <ul className="navbar-nav align-items-center ">
              <li
                ref={el =>
                  (animateNavItems.current[animateNavItems.current.length] = el)
                }
                className="nav-item"
              >
                <a
                  href="/contact.html"
                  className="nav-link nav-link-hover text-black"
                >
                  Start your project +
                </a>
              </li>
            </ul>
            */}
          </div>
        </div>
      </Router>
    </nav>
  );
};

export default Navbar;
